/* 
 * Header 
 */
.header-wrapper {
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 55px;
  margin-top: 3rem; }
  @media (min-width: 600px) {
    .header-wrapper {
      height: 60px;
      width: 94vw; } }

.greybg {
  border-top: 2px solid #14a37f;
  border-bottom: 2px solid #14a37f;
  position: relative;
  width: 100vw; }
  @media (min-width: 600px) {
    .greybg {
      width: 94vw; } }

@media (min-width: 950px) {
  .greybg-mob {
    display: none; } }

.greybg-desk {
  display: none; }
  @media (min-width: 950px) {
    .greybg-desk {
      display: block; } }

.home-container {
  display: flex;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 0 6%;
  height: 100vh; }
  @media (min-width: 600px) {
    .home-container {
      background-size: contain;
      padding: 0 10%; } }

.container {
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0 10%; }

.about {
  display: flex;
  padding: 0 1.5rem; }
  @media (min-width: 600px) {
    .about {
      padding: 0 2rem; } }
  @media (min-width: 950px) {
    .about {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: flex-start; } }

.skills {
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }

.project-container {
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 5rem; }

.form-container {
  width: 100%; }

.contact {
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

/* Title */
.reverse {
  color: #4aedc4; }
  .reverse:hover {
    color: #fff; }

.J {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 55px;
  font-weight: 600; }

.row {
  display: flex;
  align-items: flex-end;
  margin: auto; }

/** 
 * Button 
 */
.btn-wrapper {
  margin-top: 4rem;
  font-family: "OpenSans-Light", Helvetica, sans-serif; }
  @media (min-width: 600px) {
    .btn-wrapper {
      margin-top: 5rem; } }
  .btn-wrapper button {
    border: 1px solid #4aedc4;
    border-radius: 3px;
    color: #4aedc4;
    transition: 0.25s;
    font-family: inherit;
    font-weight: 550;
    letter-spacing: 1px;
    background: none;
    border: 2px solid;
    line-height: 1;
    padding: 0.9em 1.7em; }
    .btn-wrapper button:hover, .btn-wrapper button:focus {
      border-color: #4aedc4;
      color: #fff; }
    @media (min-width: 600px) {
      .btn-wrapper button {
        font-size: 14px; } }

.slide:hover,
.slide:focus {
  box-shadow: inset 0 3.25em 0 0 #4aedc4; }

/*  
 *  Scroll Button   
 */
.home-container {
  position: relative; }
  .home-container .scroll-btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #fff;
    opacity: 0.5;
    position: absolute;
    bottom: 3%;
    left: 50%;
    width: 100px; }
    .home-container .scroll-btn:hover {
      color: #14a37f;
      opacity: 1; }
    .home-container .scroll-btn p {
      margin: 0;
      margin-top: -5px; }
  .home-container .scroll-ani {
    animation: scroll-down 2.5s infinite alternate; }

@keyframes scroll-down {
  0% {
    transform: translate(0, 0); }
  20% {
    transform: translate(0, 10px); }
  40% {
    transform: translate(0, 0); } }
